export default {
  data() {
    return {
      msgcount: 0,
      messages: []
    }
  },
  computed: {
    user() {
      return this.$store.state.user.current
    }
  },
  mounted() {
    this.getMsgcount()
  },
  methods: {
    async getMsgcount() {
      if (this.user) {
        const ret = await this.$axios.get('/api/user/msgrecent')
        this.msgcount = ret.count
        this.messages = ret.messages
      }
    }
  }
}
