import Cookies from 'js-cookie'

import utils from '~/common/utils'
import UserHelper from '~/common/UserHelper'
import MsgNotice from '~/components/MsgNotice'

export default {
  components: {
    MsgNotice
  },
  data() {
    return {
      selectedCity: '北京',
      navbarActive: false,
      showCityComponent: true,
      activiePath: '',
      city_list: [],
      navBarFixed: false
    }
  },
  computed: {
    user() {
      return this.$store.state.user.current
    },
    isOwnerOrAdmin() {
      return UserHelper.isOwner(this.user) || UserHelper.isAdmin(this.user)
    },
    config() {
      return this.$store.state.config.config
    }
  },
  mounted() {
    this.city_list = utils.getCityList()
    const city = Cookies.get('city')
    if (city) {
      this.selectedCity = city
    } else {
      this.selectedCity = '北京'
      Cookies.set('city', this.selectedCity)
    }

    this.refreshPathShow(this.$route.path)

    window.addEventListener('scroll', this.watchScroll)
  },
  methods: {
    async signout() {
      try {
        await this.$store.dispatch('user/signout')
        utils.linkTo('/')
      } catch (e) {
        console.error(e)
      }
    },
    watchScroll() {
      const scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop
      //  当滚动超过 50 时，实现吸顶效果
      if (scrollTop > 49) {
        this.navBarFixed = true
      } else {
        this.navBarFixed = false
      }
    },
    selectCity(event) {
      // console.log(event)
      // console.log('selectedCity:' + this.selectedCity)
      Cookies.set('city', this.selectedCity)
      this.$toast.success('切换城市成功，可以点击刷新')
      location.reload()
      // console.log('selectCity:' + Cookies.get('city'))
    },
    toggleNav() {
      this.navbarActive = !this.navbarActive
    },
    clickNav(path) {
      this.refreshPathShow(path)
      this.$router.push({ path })
    },
    refreshPathShow(path) {
      if (path) {
        const activePath = this.getActivePath(path)
        // console.log('activePath:' + activePath)
        if (activePath === '/tweets' || activePath === '/tweet') {
          this.showCityComponent = false
        } else {
          this.showCityComponent = true
        }
        this.activiePath = activePath
      }
    },
    getActivePath(path) {
      if (path) {
        const arr = path.split('/')
        if (arr.length >= 2) {
          return arr[0] + '/' + arr[1]
        }
      }
    }
  }
}
